import api from '../../api'
import HighCharts from 'highcharts'
import ApiNotFoundError from '../../errors/ApiNotFoundError';


/**
 * Pie chart documentation
 * @date 8/3/2023 - 7:42:30 PM
 *
 * @type {{ apiUrl: string; elementId: string; chartOptions: { title: { text: string; align: string; }; tooltip: { pointFormat: string; }; }; drawAt: (elementId: any) => any; }}
 */
const ChartPie = {
  //options
  apiUrl: '',
  elementId: '',
  chartOptions: {
    title: {
      text: 'Piechart',
      align: 'left'
    },
    tooltip: {
      pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
    },
  },
  
  //actions
  drawAt: async (elementId) => {
    try {
      if (!ChartPie.apiUrl) {
        throw new ApiNotFoundError('Pie chart api url khng c  trng');
      }
      const data = await api.get(ChartPie.apiUrl)
      HighCharts.chart(elementId || ChartPie.elementId, {
        chart: {
          plotBackgroundColor: null,
          plotBorderWidth: null,
          plotShadow: false,
          type: 'pie'
        },
        accessibility: {
          point: {
            valueSuffix: '%'
          }
        },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: 'pointer',
            dataLabels: {
              enabled: true,
              format: '<b>{point.name}</b>: {point.percentage:.1f} %'
            }
          }
        },
        series: [{
          name: 'Brands',
          colorByPoint: true,
          data
        }],
        ...ChartPie.chartOptions,
      });
    } catch (e) {
      console.error(e)
    }
  }
}

export default ChartPie