import api from '../../api'
import HighCharts from 'highcharts'
import ApiNotFoundError from '../../errors/ApiNotFoundError';


/**
 * Bar chart documentation
 * @date 8/3/2023 - 7:41:22 PM
 *
 * @type {{ apiUrl: string; elementId: string; chartOptions: { title: { text: string; align: string; }; tooltip: { pointFormat: string; }; }; drawAt: (elementId: any) => any; }}
 */
const ChartBar = {
  
  //options
  apiUrl: '',
  elementId: '',
  chartOptions: {
    title: {
      text: 'Alo 123123123',
      align: 'left'
    },
    tooltip: {
      pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
    },
  },

  //actions
  drawAt: async (elementId) => {
    try {
      if (!ChartBar.apiUrl) {
        throw new ApiNotFoundError('Bar chart api url khng c  trng');
      }
      const data = await api.get(ChartBar.apiUrl)
      //draw chart
      // @ts-ignore
      HighCharts.chart(elementId || ChartBar.elementId, {
        chart: {
          type: 'column'
        },
        subtitle: {
          text:
            'Source: <a target="_blank" ' +
            'href="https://www.indexmundi.com/agriculture/?commodity=corn">indexmundi</a>',
          align: 'left'
        },
        xAxis: {
          categories: ['USA', 'China', 'Brazil', 'EU', 'India', 'Russia'],
          crosshair: true,
          accessibility: {
            description: 'Countries'
          }
        },
        yAxis: {
          min: 0,
          title: {
            text: '1000 metric tons (MT)'
          }
        },
        plotOptions: {
          column: {
            pointPadding: 0.2,
            borderWidth: 0
          }
        },
        series: data,
        ...ChartBar.chartOptions,
      });
    } catch (e) {
      console.error(e)
    }
  }
}

export default ChartBar